import conf from '../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import ville from "../../dict/ville.js";
import { storage } from "../../../firebase/firebase";
const animatedComponents = makeAnimated();


// const cities = require('all-the-cities');

registerLocale('es', es)

  const optionEtat = [
    {
      options: [
        { label: "Bien droul", value: "deroule" },
        { label: "Annul", value: "annule" },
        { label: "Repouss", value: "repousse" },
      ]
    },
  ]

class DetailMatch extends Component {
    constructor(props) {
        super(props)
        this.state = {
          ok:"",
        
        }
    }

    componentDidMount() {

      let  data=  JSON.parse(localStorage.getItem('token_auth'))
      var id=localStorage.getItem("idMatch")
      let equipe=[]
      const requestOptions = {
        method: 'GET',
        headers: { 
          'Authorization':'Bearer '+data.token,
    
        },
      };
  
      fetch(conf.url+"match/matchs/"+id,requestOptions)
      .then((response) => response.json())
      .then((data) => {
        console.log("data",data);
        $("#codevideo").val(data.codeVideoEmded);
        $("#scorevisiteur").val(data.scoreVisiteur);
        $("#scorevisite").val(data.scoreVisite);
        $("#etat").val(data.repousse);
        $("#date").val(data.date);
        $("#heure").val(data.heure);
        $("#niveautournoi").val(data.niveauTournoi);
        $("#equipevisite").val(data.equipeVisites[0].nomEquipe);
        $("#equipevisiteur").val(data.equipeVisiteurs[0].nomEquipe);
   
        })
  


    }

    handleRetour=()=>{
      this.setState({ok:<Redirect to='/detail-tournoi'/>})
    }

   
  render() {

    const { pick } = this.state
    const { selectedGroup } = this.state
    const { selectedLangue } = this.state
    const animatedComponents = makeAnimated();
    
  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}

            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Dtails du match</h1>
                  {/* </CCardHeader> */}

                  <CardBody>
                    <Row> 
                      <Col md="5">
                        <label>Equipe visiteur</label>
                        <Input
                          className="form-control"
                          type="text"
                         
                          id="equipevisiteur"
                          readOnly
                        />
                      </Col>
                      <Col md="1">
                        <label>Score</label>
                        <Input
                          className="form-control"
                          type="number"
                          min="0"
                          id="scorevisiteur"
                          readOnly
                        />
                      </Col>
                      <Col md="5">
                        <label>Equipe visit</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="equipevisite"
                          readOnly
                        />
                      </Col>
                      <Col md="1">
                        <label>Score</label>
                        <Input
                          className="form-control"
                          type="number"
                          min="0"
                          id="scorevisite"
                          readOnly
                        />
                      </Col>
                   
                    </Row>

                    <Row className="mt-4">
                    <Col md="4">
                        <label>Niveau du tournoi</label>
                        <Input
                          className="form-control"
                          type="text"
                          
                          id="niveautournoi"
                          readOnly
                        />
                      </Col>
                      <Col md="4">
                        <label>Code vido</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="codevideo"
                          readOnly
                        />
                      </Col>
                      <Col md="4">
                        <label>Etat</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="etat"
                          readOnly
                        />
                      </Col>
                    </Row>

                    <Row className="mt-4">
                    <Col md="6">
                        <label>Date du match</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="date"
                          readOnly
                        />
                      </Col>
                      <Col md="6">
                        <label>Heure du match</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="heure"
                          readOnly
                        />
                      </Col>
                    </Row>
                  
                    
                  </CardBody>
                </Card>
              </Col>
            </Row>
            <Row>
            <Col md="4">
                        <Button style={{marginBottom:"20px"}} onClick={this.handleRetour} color="danger" block className="b-lg">
                          Retour
                        </Button>
                      </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default DetailMatch